import Api from '../../api/wo';
	export default {
		onLoad:function(option){
			Api.getOPInfo(option.operationId).then(res=>{
				console.log(res)
				this.person=res.data.person
				this.operationId=option.operationId
				this.operationInfo=res.data.operationInfo
				
			})
		},
		data() {
			return {
				person: '',
				operationId: '',
				operationInfo: '',
			};
		},
		methods: {

		}
	}
